<template>
  <div class="user-wrap">
    <van-nav-bar
      title="设置"
      left-text="返回"
      left-arrow
      @click-left="onClickLeft"
    />
    <van-form @submit="onSubmit">
      <van-field name="uploader" label="">
        <template #input>
          <van-uploader v-model="uploader" style="margin-left: 40%;"/>
        </template>
      </van-field>
      <van-field
        v-model="username"
        name="用户名"
        label="用户名"
        placeholder="用户名"
        :rules="[{ required: true, message: '请填写用户名' }]"
      />
      <van-field
        v-model="password"
        type="password"
        name="支付宝账号"
        label="支付宝账号"
        placeholder="支付宝账号"
        :rules="[{ required: true, message: '请填写支付宝账号' }]"
      />
      <van-field
        v-model="password"
        type="password"
        name="微信号"
        label="微信号"
        placeholder="微信号"
        :rules="[{ required: true, message: '请填写微信号' }]"
      />
      <van-cell title="修改密码" is-link @click="goPage('/forgetPassword')" />
      <div style="margin: 16px;">
        <van-button round block type="primary" native-type="submit">
          确定修改
        </van-button>
      </div>
      <div style="margin: 16px;">
        <van-button round block type="danger" @click="loguot">
          退出登录
        </van-button>
      </div>
    </van-form>
  </div>
</template>

<script>
export default {
  name: 'index',
  components: {
  },
  data () {
    return {
      username: '',
      password: '',
      uploader: [
        // { url: 'https://img.yzcdn.cn/vant/leaf.jpg' }
      ],
    }
  },
  computed: {
  },
  mounted () {
  },
  methods: {
    goPage (url, type) {
      this.$router.push({ path: url, query: {type: type} })
    },
    onClickLeft () {
      this.$router.push({ path: '/user' })
    },
    onSubmit(values) {
      console.log('submit', values);
    },
    loguot () {
      this.$dialog.confirm({
        message: '是否退出登录'
      }).then(() => {
        this.$router.push({ path: '/login' })
      }).catch(() => {
        // on cancel
      })
    },
  }
}
</script>



